import React, { Component } from 'react';
// import axios from 'axios';
import Posts from "./posts/posts"
import './Blog.css';
import {Route,NavLink} from "react-router-dom"
import NewPost  from  "./FullPost/NewPost/NewPost"
import "../../app.css"
class Blog extends Component {
   
    componentDidMount()
    {
    
    
      setTimeout(() => { document.getElementById("id").style.display="none"
      console.log("i am body")
      
    }, 2000);
    
        
    }


onloadHandler=()=>
{
document.getElementById("id").style.display="block"
setTimeout(() => { document.getElementById("id").style.display="none"
     
      
    }, 2000);
    
}

    render () {
      
        return (
            <div>
                      <div className="body" id="id">  </div>

               <header className="blog">

                        <nav>
                            <ul>
                                <li><NavLink to="/" exact id="home">home</NavLink></li>
                                <li><NavLink to="/new-post" exact id="new_post" onClick={this.onloadHandler}>new post </NavLink></li>


                        </ul>

                        </nav>

                </header>
                {/* <Route path="/" exact render={()=><h1>home</h1>}/>
                <Route path="/"  render={()=><h1>home2</h1>}/> */}
           <Route path="/" exact component={Posts} />
           <Route path="/new-post" exact component={NewPost}/>
            </div>
        );
    }
}

export default Blog;


